* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

h1 {
  text-align: center;
  justify-content: center;
  display: flex;
}

header {
  background-color: #fff;
  width: 100%;
}

.container {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  align-items: center;
  margin-right: 20px;
  display: flex;
}

.logo img {
  height: 40px;
}

nav ul li a {
  color: #000;
  text-align: center;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: inline-block;
}

nav {
  align-items: center;
  display: flex;
}

nav ul {
  list-style: none;
  display: flex;
}

nav ul li {
  margin-left: 20px;
}

nav ul li a:hover {
  color: #00acdc;
}

nav ul li:first-child {
  margin-left: 0;
}

nav ul li:last-child {
  margin-right: 0;
}

.is-active {
  color: #00acdc;
  font-size: 12px;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

[data-qa="hover"] {
  cursor: pointer;
}
